import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { Hero }         from '../hero';
import { HeroService }  from '../hero.service';

import { Meta, Title } from '@angular/platform-browser';

@Component({
  selector: 'app-hero-detail',
  templateUrl: './hero-detail.component.html',
  styleUrls: [ './hero-detail.component.css' ]
})
export class HeroDetailComponent implements OnInit {
  @Input() hero: Hero;

  constructor(
    private route: ActivatedRoute,
    private heroService: HeroService,
    private location: Location,
    private Meta:Meta,
    private Title:Title
  ) {}

  ngOnInit(): void {
    this.getHero();
  }

  getHero(): void {
    const id = this.route.snapshot.paramMap.get('id');
    this.heroService.getHero(id)
      .subscribe(hero => {
        this.hero = hero;
        this.Title.setTitle(hero.description);
        this.Meta.removeTag("property='og:image'");
        this.Meta.removeTag("property='og:url'");
        this.Meta.removeTag("property='og:description'");
        this.Meta.removeTag("property='og:title'");
        this.Meta.removeTag("property='description'");
        this.Meta.addTag({ property: 'og:image', content: hero.images[2] });
        this.Meta.addTag({ property: 'og:url', content: 'http://www.getsalva.com/product/' + hero._id });
        this.Meta.addTag({ property: 'og:description', content: 'Descúbrelo en Salva. App para seguir precios. iOS y Android' });
        this.Meta.addTag({ property: 'og:title', content: hero.description });
        this.Meta.addTag({ property: 'description', content: 'Recibe notificaciones cuando tus productos bajen de precio. Ya disponible para iOS y Android.' });
      });
  }
}



